@import "../scss/variables";
.controlPane {
	&.controls {
		width: 250px;
		padding: 10px;

		.paneChild {
			&.color {
				flex-direction: column;
			}

			&.checkbox {
				.switch {
					display: inline-block;
					width: 48px;
					height: 18px;
					transform: translateY(0);
					position: relative;

					input {
						display: none;
					}

					input:checked + .slider:before {
						transform: translateX(30px);
						background-color: limeGreen;
					}

					input:checked + .slider {
						box-shadow: 0 0 0 2px green, 0 0 8px green;
					}
				}

				.slider {
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					right: 0;
					border-radius: 30px;
					box-shadow: 0 0 0 2px #777, 0 0 4px #777;
					cursor: pointer;
					border: 4px solid transparent;
					overflow: hidden;
					transition: 0.2s;

					&:before {
						position: absolute;
						content: "";
						width: 100%;
						height: 100%;
						background-color: #777;
						border-radius: 30px;
						transform: translateX(-30px);
						transition: 0.2s;
					}
				}
			}

			label {
				font-weight: 500;
			}
			.components-range-control{
				.components-range-control__root {
					position: relative;
					.components-range-control__number{
						margin-right: -8px;
					}
					.components-range-control__wrapper{
						span{
							&:nth-child(2){
								height: 2px;
								background: $neutral-color-200;
							}
							&:nth-child(3){
								height: 2px;
								background: $primary-color-base;
							}
						}
						.components-range-control__thumb-wrapper{
							span{
								background-color: $primary-color-base;
							}
						}
					}
					.components-button.components-range-control__reset{
						margin-bottom: 0;
						height: unset;
						padding: 0;
						box-shadow: none;
						position: absolute;
						right: 0;
						bottom: 42px;
						margin-left: 0;
						padding: 0 10px;
					}
				}
				
			}
		}
	}
}

.fb-box-shadow-wrapper {
	&.shadow-color {
		margin-bottom: 10px;
	}
    .components-base-control__field {
        position: relative;
        display: flex;
        justify-content: space-between;
		flex-direction: row;
        margin: 0;
        align-items: center;
        .components-base-control__field {
            .components-base-control__label {
                display: block;
                color: #5f5f5f;
                font-size: 12px;
                font-weight: 500;
                line-height: 16px;
                margin: 10px 0;
                min-height: auto;
                text-transform: capitalize;
            }
        }
    }
}

.fb-box-shadow-icon-dropdown {
    .fb-box-shadow-icon-btn {
        margin: 0;
        height: 28px;
        width: 28px;
        padding: 5px;
        border: 1px solid #e6e7e9;
        border-radius: 14px;
        color: #50575e;
        background: #ffffff;
        &:focus {
            outline: none;
            box-shadow: none;
        }
        .fb-box-shadow-icon {
            font-size: 15px;
            line-height: 20px;
        }
    }
}

.fb-box-shadow-label {
    margin-bottom: 15px;
}