.fb-setting-spacing-container {
	display: flex;
	flex-direction: column;
	// margin-bottom: 10px;
	.fb-setting-spacing-inner-container {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0;
		}
		.fb-setting-spacing-input-container {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding: 10px 0;
			.fb-setting-spacing-input-wrapper {
				display: flex;
				flex-direction: column;
				width: 20%;

				.fb-setting-spacing-input-label {
					text-align: left;
					font-size: 12px; 
					margin-bottom: 5px;
				}

				&:first-child {
					input {
						border-bottom-left-radius: 5px;
						border-top-left-radius: 5px;
						border-width: 1px;
					}
				}

				input {
					border-color: #ebebeb !important;
					color: #1e1e1e;
					height: 30px !important;
					border: 1px solid #8d96a0;
					border-left-width: 0;
					border-radius: 0;
					height: 24px;
					margin: 0;
					min-height: 24px;
					padding: 0;
					text-align: center;
					width: 100%;
					font-size: 14px;
					&:focus {
						outline: none !important;
						box-shadow: none !important;
					}
				}

				span {
					border-color: #ebebeb !important;
					color: #1e1e1e;
					height: 30px !important;
					border: 1px solid #8d96a0;
					border-left-width: 0;
					border-radius: 0;
					height: 24px;
					margin: 0;
					min-height: 24px;
					padding: 0;
					text-align: center;
					width: 100%;
					margin-top: 18px;
					border-bottom-right-radius: 5px;
					border-top-right-radius: 5px;
					align-items: center;
					display: flex;
					justify-content: center;
					cursor: pointer;
					&.is-checked{
						background-color: #8d96a02b;
					}
				}
			}
		}
		.fb-setting-spacing-label-container {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: 12px;
		}
	}
}
