.fb-setting-typography {
	.components-base-control {
		&.fb-typo-spacing {
			margin-bottom: 24px;
		}
	}

	.components-base-control__field {
		position: relative;
		display: flex;
		justify-content: space-between;
		margin: 0;
		align-items: center;

		.components-dropdown.fb-setting-dropdown-icon-wrapper {
			.components-button.fb-setting-dropdown-icon-btn {
				height: auto;
				padding: 6px;
			}
		}

		.fb-setting-typography-icon-dropdown {
			.fb-setting-typography-icon-btn {
				margin: 0;
				height: 28px;
				width: 28px;
				padding: 5px;
				border: 1px solid #e6e7e9;
				border-radius: 14px;
				color: #50575e;
				background: $white_color;

				&:focus {
					outline: none;
					box-shadow: none;
				}

				.fb-setting-typography-icon {
					font-size: 15px;
					line-height: 20px;
				}
			}
		}
	}
}

.fb-setting-typography-popup {
	padding: 0.2rem;
	min-width: 220px;

	.fb-setting-typography-popup-inner {
		position: relative;
		z-index: 99999;

		.components-base-control {
			&.fb-typo-spacing {
				margin-bottom: 24px;
				&:last-child{
					margin-bottom: 0;
				}
			}
		}

		.components-base-control__field {
			display: flex;
			flex-direction: column;
			margin-bottom: 0;

			.components-flex {
				gap: 0;
			}

			label {
				font-family: $heading-font-family;
				display: block;
				color: $neutral-color-950 !important;
				font-size: $base-font-size !important;
				font-weight: $font-weight-500 !important;
				line-height: 16px !important;
				// margin: 10px 0 !important;
				min-height: auto;
				text-transform: capitalize !important;
			}

			.components-base-control__label {
				color: $neutral-color-950;
				font-family: $heading-font-family;
				font-size: $base-font-size;
				font-weight: 500;
				line-height: $body-line-height;
				margin: 0 0 10px;
				min-height: auto;
				text-transform: capitalize;
			}
		}

		.fb-setting-fontsize-wrapper {
			.fb-setting-fontsize-header {
				display: flex;
				align-items: center;
				justify-content: space-between;

				.fb-setting-fontsize-label {
					display: block;
					color: $neutral-color-200;
					font-size: 12px;
					font-weight: 500;
					line-height: 16px;
					margin: 10px 0;
					min-height: auto;
					text-transform: capitalize;
				}

				.fb-setting-fontsize-units {

					.fb-setting-fontsize-units-wrap {
						direction: initial;
						display: flex;
						align-items: center;

						.fb-setting-fontsize-units-switcher {
							box-shadow: unset;
							font-size: 10px;
							padding: 0;
							text-transform: uppercase;
							padding-right: 5px;

							&.active {
								color: $primary-color;
							}
						}
					}
				}
			}
		}

		.fb-setting-letterspacing-wrapper {
			.fb-setting-letterspacing-header {
				display: flex;
				align-items: center;
				justify-content: space-between;

				.fb-setting-letterspacing-label {
					display: block;
					color: $neutral-color-200;
					font-size: 12px;
					font-weight: 500;
					line-height: 16px;
					margin: 10px 0;
					min-height: auto;
					text-transform: capitalize;
				}

				.fb-setting-letterspacing-units {
					.fb-setting-letterspacing-units-wrap {
						direction: initial;
						display: flex;
						align-items: center;

						.fb-setting-letterspacing-units-switcher {
							box-shadow: unset;
							font-size: 10px;
							padding: 0;
							text-transform: uppercase;
							padding-right: 5px;

							&.active {
								color: $primary-color;
							}
						}
					}
				}
			}
		}

		.fb-setting-lineheight-wrapper {
			.fb-setting-lineheight-header {
				display: flex;
				align-items: center;
				justify-content: space-between;

				.fb-setting-lineheight-label {
					display: block;
					color: $neutral-color-200;
					font-size: 12px;
					font-weight: 500;
					line-height: 16px;
					margin: 10px 0;
					min-height: auto;
					text-transform: capitalize;
				}

				.fb-setting-lineheight-units {
					.fb-setting-lineheight-units-wrap {
						direction: initial;
						display: flex;
						align-items: center;

						.fb-setting-lineheight-units-switcher {
							box-shadow: unset;
							font-size: 10px;
							padding: 0;
							text-transform: uppercase;
							padding-right: 5px;

							&.active {
								color: $primary-color;
							}
						}
					}
				}
			}
		}

		.components-input-control__container {
			.components-input-control__input {
				border: 1px solid $primary-color-300;

				&:focus {
					outline: none;
				}
			}

			.components-select-control__input {
				font-family: $body-font-family;
				font-size: $base-font-size;
				font-weight: 400;
				line-height: $body-line-height;
				height: unset;
				border-radius: 4px;
				padding: 14px 12px;
				border: 1px solid #E1E3E8;

				&:focus {
					outline: none;
				}
			}

			.components-input-control__backdrop {
				border: none;
			}
		}

		.components-range-control__wrapper {
			span {
				&:nth-child(2) {
					height: 2px;
					background: $neutral-color-200;
				}

				&:nth-child(3) {
					height: 2px;
					background: $primary-color-base;
				}
			}

			.components-range-control__thumb-wrapper {
				z-index: unset !important;

				span {
					background-color: $primary-color-base;
				}
			}
		}



	}
}

.fb-setting-gb-select-control-label {
	color: $neutral-color-950;
	font-family: $heading-font-family;
	font-size: $base-font-size;
	font-weight: $font-weight-500;
	line-height: $body-line-height;
	// margin: 0 0 10px;
	min-height: auto;
	text-transform: capitalize;
}

.components-base-control__field {
	.components-input-control__container {
		.components-input-control__input {
			height: 30px !important;
			min-height: 30px !important;
		}
	}
}

.fb-letter-case-control {
	.fb-control-label {
		color: $neutral-color-950 !important;
		font-family: $heading-font-family !important;
		font-size: $base-font-size !important;
		font-weight: $font-weight-500 !important;
		line-height: $body-line-height !important;
		margin-bottom: 12px;
		display: block;
	}

	.fb-case-button-group {
		display: flex;
		border: 1px solid $neutral-color-100;
		border-radius: 4px;
		overflow: hidden;
		background-color: $white-color;

		.fb-case-button {
			flex: 1;
			padding: 10px 0;
			border: none;
			border-right: 1px solid $neutral-color-100;
			background-color: $white-color;
			color: $neutral-color-700;
			font-size: 16px;
			display: flex;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			transition: background-color 0.2s;

			svg {
				width: 20px;
				height: 20px;
			}

			&:last-child {
				border-right: none;
			}

			&:hover {
				background-color: $neutral-color-100;
				border-radius: 0;
			}

			&.active {
				background-color: $neutral-color-100;
			}
		}
	}
}