.fb-blocks {
  &-container {
    padding: 32px;
    background-color: $white-color;
    border-radius: 14px;
  }

  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 42px;

    &-left, &-right {
      display: flex;
      align-items: center;
    }
  }

  &-title {
    font-family: $heading-font-family;
    font-size: 42px;
    font-weight: 600;
    line-height: 56px;
    color: $neutral-color-900;
    margin: 0;
    margin-right: 62px;
  }

  &-search {
    position: relative;
    width: $sidebar-width + $spacing-unit * 2.5;
    border: 1px solid $border-color;
    border-radius: 10px;
    display: flex;
    align-items: center;

    .fb-blocks-search-icon {
      position: absolute;
      left: $spacing-unit;
      top: 50%;
      left: 20px;
      transform: translateY(-50%);
      color: $dark-gray;
      font-size: $base-font-size * 1.4;
      z-index: 1;
      pointer-events: none;
      width: 18px;
      height: 18px;
      fill: $neutral-color;

      svg {
        width: 100%;
        height: 100%;
      }
    }
    
    &-input[type="text"] {
      width: 100%;
      padding: 18px 20px 18px 50px;
      border: none;
      border-radius: 10px;
      font-family: $body-font-family;
      font-size: 16px;
      font-weight: 400;
      line-height: 22px;
      color: $neutral-color;
      background: transparent;

      &::placeholder {
        color: $neutral-color;
      }

      &:focus {
        outline: none;
        border-color: $primary-color;
        box-shadow: 0 0 0 1px $primary-color;
      }
    }

    &-clear {
      position: absolute;
      right: $spacing-unit;
      top: 50%;
      transform: translateY(-50%);
      background: none;
      border: none;
      font-size: $base-font-size * 1.3;
      color: $dark-gray;
      cursor: pointer;
      padding: 0;
      line-height: 1;

      &:hover {
        color: $text-color;
      }
    }
  }

  &-button {
    font-family: $body-font-family;
    font-size: 16px;
    line-height: 22px;
    font-weight: 500;
    padding: 18px 32px;
    background-color: $light-gray;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color $transition-duration;

    &:hover {
      background-color: darken($light-gray, 5%);
    }

    &.fb-blocks-enable-all {
      background-color: $primary-color-base;
      color: $white-color;
      margin-left: 16px;

      &:hover {
        background-color: darken($primary-color, 5%);
      }
    }

    &.fb-blocks-disable-all {
      background-color: $neutral-color-50;
      color: $neutral-color-900;

      &:hover {
        background-color: darken($neutral-color-50, 5%);
      }
    }
  }

  &-category-filter {
    display: flex;
    flex-wrap: wrap;
    gap: $spacing-unit * 2;
    margin-bottom: $spacing-unit * 2.5;
    padding: 14px;
    border: 1px solid $neutral-color-50;
    border-radius: 14px;
  }

  &-category-button {
    padding: ($spacing-unit * 1.5) ($spacing-unit * 2);
    background-color: transparent;
    border: none;
    border-radius: $border-radius * 2.5;
    cursor: pointer;
    transition: background-color $transition-duration;
    font-family: $body-font-family;
    font-size: $medium-font-size;
    font-weight: 500;
    line-height: $body-line-height;
    color: $neutral-color-600;

    display: flex;
    justify-content: space-between;
    align-items: center;
    
    gap: $spacing-unit;

    &-title {
      font-family: $body-font-family;
      font-size: 16px;
      line-height: 22px;
      font-weight: 500;
    }

    &-icon {
      display: flex;
      svg {
        width: 20px;
        height: 20px;
      }
    }

    &-count {
      font-size: 12px;
      color: $neutral-color;
      background-color: $primary-color-50;
      border-radius: 50%;
      width: 24px;
      height: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &:hover {
      background-color: $primary-color-50;
      color: $primary-color;

      .fb-blocks-category-button-count {
        background-color: $white-color;
        color: $neutral-color;
      }
    }

    &--active {
      background-color: $primary-color-50;
      color: $primary-color;

      .fb-blocks-category-button-count {
        background-color: $white-color;
        color: $neutral-color;
      }

      &:hover {
        background-color: darken($primary-color-50, 5%);
      }
    }
  }

  &-category-group {
    margin-bottom: $spacing-unit * 3.75;
  }

  &-category-title {
    font-family: $heading-font-family;
    font-size: 24px;
    font-weight: 600;
    line-height: 31px;
    margin-top: 42px;
    margin-bottom: 24px;
    color: $neutral-color-900;
  }

  &-category-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $spacing-unit * 2.5;
  }

  &-no-results {
    font-style: italic;
    color: $dark-gray;
  }
}

.frontis-blocks-search {
  position: relative;
  width: 300px;

  &::before {
    content: '\f179'; // WordPress dashicons search icon
    font-family: dashicons;
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: #757575;
    font-size: 20px;
    z-index: 1;
    pointer-events: none; // This ensures the icon doesn't interfere with input interactions
  }

  .frontis-blocks-search-input {
    width: 100%;
    padding: 8px 12px 8px 35px; // Increased left padding to accommodate the icon
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;

    &:focus {
      outline: none;
      border-color: #007cba;
      box-shadow: 0 0 0 1px #007cba;
    }

    &::placeholder {
      color: #999;
    }
  }
}

.fb-block-card {
  display: flex;
  align-items: center;
  padding: $spacing-unit * 2.5 $spacing-unit * 2;
  border-radius: 14px;
  background-color: $neutral-color-50;
  transition: box-shadow $transition-duration;

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }

  &-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    margin-right: 12px;

    img {
      width: 34px;
      height: auto;
    }
  }

  &-content {
    flex: 1;
  }

  &-title {
    font-family: $body-font-family;
    line-height: 25px;
    font-size: 18px;
    font-weight: 500;
    color: $neutral-color-900;
    margin: 0 0 6px;
    display: flex;
    align-items: center;

    .fb-block-card-badge {
      padding: 2px 4px;
      border-radius: 4px;
      font-family: $body-font-family;
      font-size: 10px;
      font-weight: 600;
      line-height: 11px;
      color: $primary-color-50;
      background-color: $warning-color;
      margin-left: 4px;

      &:first-child {
        margin-left: 8px;
      }

      &.new {
        background-color: $primary-color-base;
      }
    }
  }

  &-links {
    display: flex;
    gap: 14px;
  }

  &-link {
    font-size: $medium-font-size;
    color: $neutral-color;
    text-decoration: none;
    display: inline-flex;
    justify-content: center;
    transition: color $transition-duration;

    svg {
      height: $spacing-unit * 2;
      width: $spacing-unit * 2;
      margin-right: $spacing-unit / 2;
    }

    &:hover {
      color: $primary-color;
      text-decoration: none;
    }
  }

  &-switch {
    flex: 0 0 auto;
    margin-left: $spacing-unit * 2;

    &-label {
      display: flex;
      align-items: center;

      .rc-switch {
        width: 44px;
        height: 24px;
        border-radius: ($border-radius * 8);

        &::after {
          left: 1px;
          top: 1px;
        }
      }
      .rc-switch-checked:after {
        left: auto;
      }

      .fb-block-card-upcoming {
        font-family: $body-font-family;
        font-size: 12px;
        font-weight: 600;
        line-height: 15.6px;
        color: $white-color;
        background-color: $error-color-400;
        padding: 6px 8px;
        border-radius: 4px;
      }
    }

    &-input {
      margin-left: $spacing-unit;
    }
  }
}