@import "../scss/variables";
.fb-unit-wrapper {
	display: flex;
	align-items: center;
    border-radius: 6px;
    background: $neutral-color-50;
	height: 28px;
	.fb-reset-control-container {
        padding: 6px 10px;
        border-right: 1px solid $white-color;
		.fb-reset-button {
            background-color: unset;
			align-items: center;
			border: 0;
			cursor: pointer !important;
			display: flex;
			justify-content: center;
			min-width: auto !important;
			opacity: 1 !important;
			svg {
				display: block;
				fill: none;
			}
		}
	}
}
.fb-setting-fontsize-wrapper, .fb-setting-letterspacing-wrapper, .fb-setting-lineheight-wrapper {
    .components-range-control__reset {
        position: absolute;
        top: -33px;
        right: 80px;
        box-shadow: unset;
        font-size: $unit-font-size;
        &:hover {
            box-shadow: unset !important;
        }
        &:focus {
            outline: none !important;
            box-shadow: none !important;
        }
    }
}


.fb-setting-units-type {
	position: relative;
	display: inline-block;
    padding: 6px 10px;
	.fb-setting-units-current {
        font-family: $heading-font-family;
        font-size: $unit-font-size;
		font-weight: 400;
		border: none;
		cursor: pointer;
		text-align: center;
		text-transform: uppercase;
		display: flex;
		align-items: center;
		justify-content: center;
		color: $primary-color-base;
	}

	.fb-setting-units-dropdown {
		position: absolute;
		top: 2px;
		left: 0;
		z-index: 10;
		background: #F7F7F7;
		overflow: hidden;
		width: 100%;
		border-radius: 4px;
		.fb-setting-units-option {
			cursor: pointer;
			transition: background 0.2s;
			height: 26px;
			display: flex;
			align-items: center;
			justify-content: center;
			text-transform: uppercase;
			font-family: $heading-font-family;
			font-size: $unit-font-size;
			font-weight: 400;
			&.active {
				font-weight: 400;
				color: $primary-color-base;
			}
			&:hover {
				background: $neutral-color-100;
			}
		}
	}
}
