.fb-blocks-settings {
  font-family: $body-font-family;
  color: $text-color;
  background-color: $white-color;
  border-radius: 14px;

  .fb-button-upcoming {
    cursor: default;
    font-family: $body-font-family;
    font-size: 14px;
    font-weight: 500;
    line-height: 19.6px;
    background-color: $neutral-color-50;
    border-radius: $border-radius * 2;
    padding: 12px 16px;
    color: $neutral-color-900;
    margin: 0;
  }

  h2 {
    font-size: 23px;
    font-weight: 400;
    margin: 0;
    padding: $spacing-unit 0 ($spacing-unit / 2) 0;
    line-height: 1.3;
  }

  .fb-blocks-settings-container {
    display: flex;
  }

  .fb-blocks-settings-tabs {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: $sidebar-width;
    border-right: 1px solid $border-color;
    padding: 42px;
  }

  .fb-blocks-settings-tab {
    padding: ($spacing-unit * 2 + 2) ($spacing-unit * 2 + 4);
    text-align: left;
    background: none;
    border: none;
    cursor: pointer;
    transition: background-color $transition-duration;
    font-family: $body-font-family;
    font-size: $x-large-font-size;
    font-weight: 500;
    color: $neutral-color-600;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    border-radius: ($border-radius * 3);

    &-icon {
      svg {
        width: 24px;
        height: 24px;
      }
    }

    &:hover {
      background-color: $primary-color-50;
    }

    &.active {
      background-color: $primary-color-50;
      color: $primary-color;
    }
  }

  .fb-blocks-settings-content {
    flex-grow: 1;
  }

  .fb-blocks-settings-options {
    h3 {
      font-size: 1.3em;
      margin-bottom: 20px;
      color: #23282d;
    }

    .fb-option {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      gap: 16px;
      padding: 42px;
      border-bottom: 1px solid #e2e4e7;

      &:last-child {
        border-bottom: none;
      }

      .fb-option-label {
        flex: 1;

        h3 {
          font-family: $heading-font-family;
          font-weight: 600;
          margin-top: 0;
          margin-bottom: 14px;
          display: block;
          font-size: $h5-font-size;
          color: $neutral-color-900;
          line-height: $heading-line-height;
        }

        p {
          font-family: $body-font-family;
          font-size: 16px;
          font-weight: 400;
          color: $neutral-color-600;
          line-height: 22px;
          margin: 0;
        }
      }

      .fb-option-input {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        .components-base-control__field {
          margin-bottom: 0;
        }

        .components-text-control__input {
          width: 70px;
        }

        .fb-button-primary {
          font-family: $body-font-family;
          font-size: 14px;
          font-weight: 500;
          line-height: 19.6px;
          background-color: $primary-color-base;
          border-radius: $border-radius * 2;
          padding: 12px 16px;
        }

        &-wrapper {
          display: flex;
          align-items: center;
          gap: 6px;

          .fb-option-input-with-px {
            display: flex;
            align-items: center;
            position: relative;
            background-color: $neutral-color-50;
            border-radius: 6px;
            overflow: hidden;

            .components-base-control__field {
              margin-bottom: 0;
              margin-top: 0;
            }

            .components-text-control__input {
              border: none;
              border-radius: 0;
              background-color: transparent;
              padding: $input-padding ($input-padding + 6);
              font-family: $body-font-family;
              font-size: 14px;
              font-weight: 500;
              line-height: 17px;
              color: $neutral-color-600;

              &:focus {
                outline: none;
                box-shadow: none;
              }
            }
          }

          .fb-option-input-px {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: $input-padding ($input-padding + 6);
            background-color: $white-color;
            border: 1px solid $neutral-color-50;
            border-left: none;
            border-top-right-radius: 6px;
            border-bottom-right-radius: 6px;
            color: $neutral-color-600;
            font-size: 14px;
            font-weight: 500;
            line-height: 16px;
            pointer-events: none;
          }
          .fb-responsive-device-wrapper {
            margin-right: 0;
            height: 33px;

            .fb-device-switcher.active {
              height: 33px;
            }
          }
        }

        &-rollback {
          flex: 0 0 300px;
          flex-direction: row;
          justify-content: flex-end;
          align-items: center;

          .components-select-control {
            flex: 1;
            margin-right: 8px;

            .components-select-control__input {
              height: auto;
              width: auto;
              padding: 10px 46px 10px 18px;
              background-color: $neutral-color-50;
              border: none;
              border-radius: $border-radius * 2;
              color: #6A758E;
              font-family: $body-font-family;
              font-size: 14px;
              font-weight: 500;
              line-height: 19.6px;

              &:focus {
                border-radius: $border-radius * 2;
              }
            }
            .components-input-control-suffix-wrapper {
              padding-right: 18px;

              svg {
                width: 22px;
                height: 22px;
                color: #6A758E;
                fill: #6A758E;
              }
            }
            .components-input-control__backdrop {
              border: none;

              &:focus {
                border-radius: $border-radius * 2;
              }
            }
          }

          .fb-button-primary {
            flex: 0 0 auto;
            height: 40px;
            font-family: $body-font-family;
            font-size: 14px;
            font-weight: 500;
            line-height: 19.6px;
            background-color: $primary-color-base;
            border-radius: $border-radius * 2;
            padding: 10px 18px;
            color: $white-color;

            &:hover {
              background-color: darken($primary-color, 10%);
              color: $white-color;
            }
          }
        }

        .rc-switch {
          width: 44px;
          height: 24px;
          border-radius: ($border-radius * 8);

          &::after {
            left: 1px;
            top: 1px;
          }
        }
        .rc-switch-checked:after {
          left: auto;
        }
      }
    }
  }

  // Responsive styles
  @media screen and (max-width: 782px) {
    .fb-blocks-settings-editor-options {
      .fb-option {
        flex-direction: column;

        .fb-option-label {
          padding-right: 0;
          margin-bottom: 10px;
        }

        .fb-option-input {
          flex: 0 0 auto;
          width: 100%;
        }
      }
    }
  }
}
