.dnd-items-wrapper {
    li.sortable-item {
        .sortable-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: $neutral-color-50;
            padding: 12px;
            border: none;
            border-radius: 4px;
            height: 44px;

            .sortable-sort,
            .sortable-duplicate,
            .sortable-delete {
                border: none;

                .components-button {
                    svg {
                        fill: none;
                        width: 16px;
                        height: 16px;
                    }
                }

                .components-button {
                    background: none;
                    margin-bottom: 0;
                    padding: 0;
                    height: unset;
                    &.has-icon{
                        justify-content: flex-end;
                        border-radius: 0 !important;
                    }
                }
            }

            .sortable-title {
                font-family: $body-font-family;
                font-size: $base-font-size;
                font-weight: $font-weight-500;
                color: $neutral-color-700;
                line-height: 18px;
            }
        }
    }
}

.fb-feature-wrapper {
    .components-button.is-primary {
        font-weight: $font-weight-500;
        background: $primary-color-base;
        color: $white-color;
        padding: 10px 16px;
        border-radius: 4px;
        margin-bottom: 24px;
    }
}